import React from 'react';
import { Table, Typography, Divider, Popover, message } from 'antd';
import {
	EditTwoTone,
	DownloadOutlined,
	LoadingOutlined,
	PlayCircleTwoTone,
} from '@ant-design/icons';
import axios from 'axios';
import fileDownload from 'js-file-download';

import { fetchActiveProject, setTableLoading, setTableData } from '../redux/actions/projects';
import { useSelector, useDispatch } from 'react-redux';

import EditingModalForm from './EditingModalForm';
import AudioPlayere from './AudioPlayere';

const ProjectTable = () => {
	const dispatch = useDispatch();

	const [editingRow, setEditingRow] = React.useState(null);
	const [audioSavingId, setAudioSavingId] = React.useState(null);
	const [confirmLoading, setConfirmLoading] = React.useState(false);

	const [audioSource, setAudioSource] = React.useState(null);
	const [rowAudio, setRowAudio] = React.useState({});

	console.log('rowAudio', rowAudio);
	console.log('audioSource', audioSource);

	const {
		activeProject,
		tableLoading,
		tableColumns,
		tablePaginationConfig,
		tableData,
		searchParams,
		userInfo,
	} = useSelector(({ projects }) => projects);

	const onCreate = (record) => {
		setConfirmLoading(true);
		const editRow = {
			project: activeProject.value,
			...record,
		};

		axios
			.patch(`/project/editrow/${record.id}`, editRow)
			.then(({ data }) => {
				message.success(data.message);
				const newTableData = tableData.map((item) => {
					if (item.id === record.id) {
						return { ...data.row };
					}
					return item;
				});
				dispatch(setTableData(newTableData));
			})
			.catch(() => {
				message.error('Произошла ошибка');
			})
			.finally(() => {
				setConfirmLoading(false);
				setEditingRow(null);
			});
	};

	const onChangeTablePage = async (page, pageSize) => {
		dispatch(setTableLoading(true));
		if (pageSize > 15) {
			window.scrollTo({
				top: 172,
				behavior: 'smooth',
			});
		}
		const parameters = {
			project: activeProject.value,
			page: page,
			per_page: pageSize,
			from: searchParams ? searchParams.from : null,
			to: searchParams ? searchParams.to : null,
			phone: searchParams ? searchParams.phone : null,
			status: searchParams ? searchParams.status : null,
		};
		dispatch(fetchActiveProject(parameters));
	};

	const downloadAudio = (record) => {
		// console.log(record.request_id);
		setAudioSavingId(record.id);
		const correctTime = record.time.replace(/[^\d]/g, '_');
		axios({
			url: `/da/${record.request_id}`,
			method: 'POST',
			responseType: 'blob',
		})
			.then(({ data }) => {
				fileDownload(data, correctTime + '_' + record.number + '.mp3');
			})
			.finally(() => {
				setAudioSavingId(null);
			});
	};

	const playAudio = (record) => {
		if (rowAudio[record.id]) {
			if (rowAudio[record.id] === audioSource.url) {
				console.log('одинаковая аудио запись');
				return;
			}
			setAudioSource({
				id: record.id,
				url: rowAudio[record.id],
			});
			return;
		}
		setAudioSource(null);
		axios({
			url: `/da/${record.request_id}`,
			method: 'POST',
			responseType: 'blob',
		}).then(({ data }) => {
			const url = URL.createObjectURL(data);
			setAudioSource({
				id: record.id,
				url: url,
			});

			const newArr = {
				...rowAudio,
				[record.id]: url,
			};
			setRowAudio(newArr);
		});
	};

	const columns =
		userInfo.isadmin === 1 || userInfo.username === 'supervisor'
			? [
					...tableColumns,
					{
						title: '#',
						dataIndex: 'actions',
						width: 50, //def 122, mid 35
						render: (_, record) => (
							<>
								<Typography.Link onClick={() => setEditingRow(record)}>
									<EditTwoTone style={{ fontSize: 18 }} />
								</Typography.Link>
								{userInfo.isadmin === 1 ? (
									<>
										<Divider type="vertical" />
										<Popover
											content={
												audioSource &&
												audioSource.url === rowAudio[audioSource.id] ? (
													<AudioPlayere src={audioSource} />
												) : (
													'загрузка'
												)
											}
											trigger="click">
											<Typography.Link onClick={() => playAudio(record)}>
												<PlayCircleTwoTone
													style={{ fontSize: 18 }}
													twoToneColor={
														record.id === audioSource?.id
															? '#52c41a'
															: false
													}
												/>
											</Typography.Link>
										</Popover>
										<Divider type="vertical" />
										<Typography.Link onClick={() => downloadAudio(record)}>
											{record.id === audioSavingId ? (
												<LoadingOutlined style={{ fontSize: 18 }} />
											) : (
												<DownloadOutlined style={{ fontSize: 18 }} />
											)}
										</Typography.Link>
									</>
								) : null}
							</>
						),
					},
			  ]
			: tableColumns;

	return (
		<>
			<Table
				size="middle"
				className="project-table"
				rowKey={(record) => record.id}
				columns={columns}
				// scroll={{ x: columns.length > 14 ? 1900 : false }}
				dataSource={tableData}
				loading={tableLoading}
				pagination={{
					position: ['bottomCenter'],
					current: tablePaginationConfig.current_page,
					total: tablePaginationConfig.total,
					pageSize: tablePaginationConfig.per_page,
					onChange: onChangeTablePage,
					hideOnSinglePage: true,
				}}
				bordered
			/>
			{editingRow && (
				<EditingModalForm
					onCreate={onCreate}
					onCancel={() => {
						setEditingRow(null);
					}}
					formRows={tableColumns}
					editingRow={editingRow}
					confirmLoading={confirmLoading}
				/>
			)}
		</>
	);
};

export default ProjectTable;
